import { Component, OnDestroy, OnInit } from '@angular/core';
import { CompareService } from '../../services/listen.service';
import { Subscription } from 'rxjs/index';
import { ElMessageService } from 'element-angular/release/message/message.service';

@Component({
  selector: 'app-compare-pop',
  templateUrl: './compare-pop.component.html',
  styleUrls: ['./compare-pop.component.css']
})
export class ComparePopComponent implements OnInit, OnDestroy {
  subscription: Subscription;
  showCompare: boolean;
  compareData: any = [];
  itemsCase = [{}, {}, {}, {}];
  constructor(private compare: CompareService, private message: ElMessageService) {
    this.subscription = this.compare.get().subscribe(res => {
      this.showCompare = true;
      if (res.state) {
        if (this.compareData.length >= 4) {
          this.message['warning']('对比栏已满！ ');
        } else {
          this.compareData.push(res.data);
        }
      } else {
        this.deleteItem(res.data);
      }
    });
  }

  ngOnInit() {
  }
  ngOnDestroy() {
    this.subscription.unsubscribe();
  }
  closeCompare() {
    this.showCompare = false;
  }
  deleteItem(item) {
    let index = -1;
    index = this.getIndex(this.compareData, item);
    if (index !== -1) {
      this.compareData.splice(index, 1);
    }
  }
  deleteAll() {
    this.compareData = [];
  }
  getIndex(_arr, _obj) {
    const len = _arr.length;
    for (let i = 0; i < len; i++) {
      if (JSON.stringify(_arr[i]) === JSON.stringify(_obj)) {
        return i;
      }
    }
    return -1;
  }
}
