import {trigger, state, style, transition, animate} from '@angular/animations';

export const menu = trigger('menu', [
  state('on', style({
    display: 'block',
    opacity: '1'
  })), /*开*/
  state('off', style({
    display: 'none',
    opacity: '0',
    height: '0'
  })), /*关*/
  transition('on => off', animate('300ms')),
  transition('off => on', animate('300ms'))
]);
