import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule } from '@angular/router';
import { LayoutModule } from '../layout/layout.module';
import { LayoutComponent } from '../layout/layout.component';
import { HomeComponent } from './home/home.component';
import { GridsterModule } from 'angular-gridster2';
import { ElModule } from 'element-angular/release/element-angular.module';
import { ComponentsModule } from '../public/components/components.module';
import { CommonPagesModule} from './common-pages/common-pages.module';
import { DetailComponent } from './common-pages/detail/detail.component';
import { ContrastComponent } from './common-pages/contrast/contrast.component';
import { ReviewComponent } from './common-pages/review/review.component';

export const mainRoutes = [
  {
    path: '',
    component: LayoutComponent,
    children: [
      {path: '', redirectTo: 'home', pathMatch: 'full'},
      {path: 'home', component: HomeComponent},
      {path: 'detail', component: DetailComponent},
      {path: 'contrast', component: ContrastComponent},
      {path: 'review', component: ReviewComponent},
      {path: 'userManage', loadChildren: './user-manage/user-manage.module#UserManageModule'},
      {path: 'projectManage', loadChildren: './project-manage/project-manage.module#ProjectManageModule'},
      {path: '**', redirectTo: 'home'}
    ]
  }
];
@NgModule({
  declarations: [HomeComponent],
  imports: [
    CommonModule,
    LayoutModule,
    RouterModule.forChild(mainRoutes),
    ElModule,
    GridsterModule,
    ComponentsModule,
    CommonPagesModule
  ]
})
export class MainModule {
}
