import { Component, OnInit } from '@angular/core';
import { menu } from '../../public/animation/menu';
import { ListenService } from '../../public/services/listen.service';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css'],
  animations: [menu]
})
export class HeaderComponent implements OnInit {
  navState = 'off';
  constructor(private  listenService: ListenService) { }

  ngOnInit() {
  }
  openNav() {
    this.navState = 'on';
  }
  closeNav() {
    this.navState = 'off';
  }
  editHome() {
    this.listenService.edit.emit(true);
  }
}
