import { Component, OnInit } from '@angular/core';
import { menu } from '../../public/animation/menu';

@Component({
  selector: 'app-menu',
  templateUrl: './menu.component.html',
  styleUrls: ['./menu.component.css'],
  animations: [menu]
})
export class MenuComponent implements OnInit {
  menuActive = -1;
  constructor() { }

  ngOnInit() {
  }
  openSubmenu(index) {
    this.menuActive = index;
  }
  closeSubmenu() {
    this.menuActive = -1;
  }
  menuState(index) {
    if (this.menuActive === index) {
      return 'on';
    } else {
      return 'off';
    }
  }
  test() {
    console.log(1234);
  }
}
