import { ChangeDetectionStrategy, Component, OnInit, ViewEncapsulation } from '@angular/core';
import {
  CompactType, DisplayGrid, GridsterComponent, GridsterConfig, GridsterItem, GridsterItemComponent, GridsterItemComponentInterface,
  GridsterPush,
  GridType
} from 'angular-gridster2';
import { ListenService } from '../../public/services/listen.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css'],
  // changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None
})
export class HomeComponent implements OnInit {
  options: GridsterConfig;
  dashboard: Array<GridsterItem>;
  itemToPush: GridsterItemComponent;
  editState: boolean;

  constructor(private homeService: ListenService) {
  }

  static eventStart(item: GridsterItem, itemComponent: GridsterItemComponentInterface, event: MouseEvent) {
    console.log('eventStart', item, itemComponent, event);
  }

  static eventStop(item: GridsterItem, itemComponent: GridsterItemComponentInterface, event: MouseEvent) {
    console.log('eventStop', item, itemComponent, event);
  }

  static overlapEvent(source: GridsterItem, target: GridsterItem, grid: GridsterComponent) {
    console.log('overlap', source, target, grid);
  }

  ngOnInit() {
    this.options = {
      gridType: GridType.Fit,
      displayGrid: DisplayGrid.None,
      pushItems: true,
      swap: false,
      draggable: {
        delayStart: 0,
        enabled: false,
        ignoreContentClass: 'gridster-item-content',
        ignoreContent: false,
        dragHandleClass: 'drag-handler',
        stop: HomeComponent.eventStop,
        start: HomeComponent.eventStart,
        dropOverItems: true,
        dropOverItemsCallback: HomeComponent.overlapEvent
      },
      resizable: {
        enabled: false
      },
      margin: 20,
      outerMargin: false,
      outerMarginTop: 20,
      outerMarginRight: 20,
      outerMarginBottom: 20,
      outerMarginLeft: 20,
    };

    this.dashboard = [
      {cols: 1, rows: 1, y: 0, x: 0, name: 'weather', index: 1},
      {cols: 1, rows: 1, y: 0, x: 1, name: 'a'},
      {cols: 1, rows: 1, y: 1, x: 0, name: 'b'},
      {cols: 1, rows: 1, y: 1, x: 1, name: 'c'},
      {cols: 2, rows: 2, y: 2, x: 0, name: 'd'},
      {cols: 1, rows: 4, y: 0, x: 2, name: 'note'},
    ];
    this.edit();
  }

  changedOptions() {
    if (this.options.api && this.options.api.optionsChanged) {
      this.options.api.optionsChanged();
    }
  }

  removeItem($event, item) {
    $event.preventDefault();
    $event.stopPropagation();
    this.dashboard.splice(this.dashboard.indexOf(item), 1);
  }

  addItem() {
    this.dashboard.push({x: 0, y: 0, cols: 1, rows: 1});
  }

  initItem(item: GridsterItem, itemComponent: GridsterItemComponent) {
    this.itemToPush = itemComponent;
  }

  pushItem() {
    const push = new GridsterPush(this.itemToPush); // init the service
    this.itemToPush.$item.rows += 4; // move/resize your item
    if (push.pushItems(push.fromNorth)) { // push items from a direction
      push.checkPushBack(); // check for items can restore to original position
      push.setPushedItems(); // save the items pushed
      this.itemToPush.setSize();
      this.itemToPush.checkItemChanges(this.itemToPush.$item, this.itemToPush.item);
    } else {
      this.itemToPush.$item.rows -= 4;
      push.restoreItems(); // restore to initial state the pushed items
    }
    push.destroy(); // destroy push instance
    // similar for GridsterPushResize and GridsterSwap
  }

  edit() {
    this.homeService.edit.subscribe((value: boolean) => {
      this.editState = value;
      if (value === true) {
        this.options.displayGrid = DisplayGrid.Always; // 显示网格
        this.options.draggable.enabled = true; // 打开元素的拖动状态
        this.options.resizable.enabled = true; // 打开元素编辑大小状态
        this.changedOptions();
      }
    });
  }

  cancelEdit() {
    this.options.displayGrid = DisplayGrid.None; // 隐藏网格
    this.options.draggable.enabled = false; // 关闭元素的拖动状态
    this.options.resizable.enabled = false; // 关闭元素编辑大小状态
    this.changedOptions();
    this.homeService.edit.emit(false);
  }
}
